import { Component, OnInit} from "@angular/core"
import { FormGroup, FormBuilder, Validator, Validators, FormControl} from "@angular/forms"

@Component({
  selector: "app-sign-up",
  template: `
    <form (ngSubmit)="onSubmit()" [formGroup]="formSignUp">
      <input
        type="email"
        placeholder="Email"
        formControlName="email"
      >
      <p *ngIf="formSignUp.controls.email.invalid && formSignUp.controls.email.touched">
        Email is required
      </p>
      <br><br>
      <input
        type="password"
        placeholder="Password"
        formControlName="password"
      >

      <br><br>
      <div formGroupName="subject">
        <label><input type="checkbox" name="nam" formControlName="nam">Nam</label>
        <label><input type="checkbox" name="nu" formControlName="nu">Nu</label>
      </div>
      <br><br>
      <button [disabled]="formSignUp.invalid">Submit</button>
    </form>
    <code>{{formSignUp.controls.email.errors | json}}</code>
  `
})

export class SignUpComponent implements OnInit{
  formSignUp: FormGroup;

  constructor(private fb:FormBuilder){
  }

  ngOnInit(): void {
    this.formSignUp = this.fb.group({
      email: ['', [Validators.email ,emailValidator]],
      password: "",
      subject: this.fb.group({
        nam: true,
        nu: false
      })
    })
  }
  onSubmit(){
    console.log(this.formSignUp.value)
  }
}

function emailValidator(formControl: FormControl){
  if(formControl.value.includes("@gmail.com")){
    return null;
  }
  return {email:true}
}
