import { Component } from "@angular/core"
import {SignInService} from './sign-in.service'

@Component({
  selector: "app-sign-in",
  // template: `
  //   <form (submit)="onSubmit(formSignIn)" #formSignIn = "ngForm">
  //     <input type="email" placeholder="Email" [(ngModel)]="email" name="email" required>
  //     <p *ngIf="formSignIn.controls.email?.errors?.required">
  //       Email is required
  //     </p>
  //     <br><br>
  //     <input type="password"placeholder="Password" [(ngModel)]="password" name="password" required>
  //     <p *ngIf="formSignIn.controls.password?.errors?.required">
  //       Password is required
  //     </p>
  //     <br><br>
  //     <button [disabled]="formSignIn.invalid">Submit</button>
  //   </form>
  // `

  template: `
    <form (submit)="onSubmit(formSignIn)" #formSignIn = "ngForm">
      <input
        type="email"
        placeholder="Email"
        ngModel
        #txtEmail="ngModel"
        name="email"
        required
        email
      >
      <br><br>
      <input
        type="password"
        placeholder="Password"
        ngModel
        #txtPassword="ngModel"
        name="password"
        password
        minlength ="8"
        pattern="[a-z]*"
      >
      <br><br>
      <div ngModelGroup="subjects">
        <label><input type="checkbox" ngModel name="nam">Nam</label>
        <label><input type="checkbox" ngModel name="nu">Nu</label>
      </div>
      <br><br>
      <button>Submit</button>
    </form>
    <br>
    <p>{{txtEmail.errors | json}}</p>
    <p>{{txtPassword.errors | json}}</p>
    <p>{{formSignIn.value | json}}</p>
  `,

  // template: `
  //   <form (submit)="onSubmit(formSignIn)" #formSignIn = "ngForm">
  //     <input placeholder="Email" ngModel #txtEmail="ngModel" name="email" required email>
  //     <p *ngIf="txtEmail.touched && txtEmail.errors?.required">
  //       Email is required
  //     </p>
  //     <p *ngIf="txtEmail.touched && txtEmail.errors?.email">
  //       Email is not valid
  //     </p>
  //     <br><br>
  //     <input type="password" placeholder="Password" ngModel="password" name="password" required>
  //     <br><br>
  //     <button [disabled]="formSignIn.invalid">Submit</button>
  //   </form>
  //   <p>{{txtEmial.errors | json }}</p>
  // `
  providers : [SignInService]
})

export class SignInComponent {
  constructor(private signInService:SignInService){}
  email = "";
  password = "";
  checkbox = "";

  onSubmit(formSignIn){
    this.signInService.sendPost(formSignIn.value)
    .then(result => console.log(result))
    .catch(err => console.log(err));
  }
}
